.App {
  text-align: center;
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

a {
  color: #FFFFFF;
  text-decoration: none;
}

a:hover {
  color:#FFFFFF;
  text-decoration:none;
  cursor:pointer;
}
